.hero-bg {
  background: linear-gradient(to right, #fff, #007bff);
}

.main-content {
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  min-height: 73vh;
  padding: 40px;
  display: flex;
}

.main-image img {
  max-width: 100%;
}

.main-text {
  color: #333;
  text-align: center;
  flex-direction: column;
  flex: 1;
  justify-content: center;
  padding-left: 40px;
  display: flex;
}

.main-text h1 {
  margin-bottom: 20px;
  font-size: 30px;
}

.main-text p {
  font-size: 18px;
}

@media screen and (min-width: 768px) {
  .main-content {
    flex-direction: row;
  }

  .main-image, .main-text {
    width: calc(50% - 10px);
  }
}

